import React from 'react'
import { ApplicationContext } from '../lib'
import Toolbar from '../components/toolbar'
import { Footer } from '../components/footer'
import { FaEnvelope, FaExclamationCircle, FaEyeSlash, FaEye, FaKey, FaUser, FaBriefcase } from 'react-icons/fa'
import { NavLink, Redirect } from 'react-router-dom'
import { useToasts } from 'react-toast-notifications'

export const Login = ({ redirectPath, registerPath, onLogin }: { onLogin: (a) => Promise<any>, redirectPath: string, registerPath: string }) => {
    const [state, updateState] = React.useState({ error: null, email: "", password: "", isLoading: false })
    const [showPassword, updateShowPassword] = React.useState(false)
    const [redirect, setRedirect] = React.useState(false)
    const { addToast } = useToasts()

    return (
        <ApplicationContext.Consumer>{(ctx) => {
            return (
                <form onSubmit={(ev) => {
                    ev.preventDefault(); ev.stopPropagation();
                    updateState({ ...state, isLoading: true })

                    onLogin(state).then(() => {
                        console.log('login success')
                        addToast('Login Success!', { appearance: 'success', autoDismiss: true })
                        updateState({ ...state, isLoading: false })

                        return setRedirect(true)
                    })
                        .catch(e => {
                            updateState({ ...state, isLoading: false, error: e.message })
                        })
                }}>
                    {redirect ? <Redirect to={redirectPath} /> : null}
                    <div className="card">
                        <section className="card-content">
                            {state.error ?
                                <div className='notification is-warning'>
                                    <FaExclamationCircle /> {state.error}
                                </div>
                                : null}

                            <div className='field is-horizontal'>
                                <div className="field-label">
                                    <label className="label">Email</label>
                                </div>
                                <div className='field-body'>
                                    <div className="field">
                                        <div className="control has-icons-left">
                                            <input required name='email' value={state.email} onChange={(ev) => updateState({ ...state, email: ev.target.value })} className="input" type="email" placeholder="Enter email address" />
                                            <span className="icon is-small is-left">
                                                <FaEnvelope />
                                            </span>
                                        </div>
                                    </div>
                                </div>
                            </div>

                            <div className='field is-horizontal'>
                                <div className="field-label">
                                    <label className="label">Password</label>
                                </div>
                                <div className='field-body'>
                                    <div className="field has-addons">
                                        <p className="control is-expanded">
                                            <input required name='password' value={state.password} onChange={(ev) => updateState({ ...state, password: ev.target.value })} className="input" type={showPassword ? 'text' : 'password'} />
                                        </p>
                                        <p className="control">
                                            <button tabIndex={-1} type='button' className="button" onClick={() => updateShowPassword(!showPassword)}>{showPassword ? <FaEyeSlash /> : <FaEye />}</button>
                                        </p>
                                    </div>
                                </div>
                            </div>

                            <div className='field'>
                                <div className='control'>
                                    <button type='submit' className={`is-success button ${state.isLoading ? 'is-loading' : ''}`} disabled={state.isLoading}>Login</button>
                                </div>
                            </div>

                        </section>

                        <footer className="card-footer is-unselectable">
                            <NavLink to={registerPath} exact className="card-footer-item">Create New Account</NavLink>
                        </footer>
                    </div>
                </form>
            )
        }}
        </ApplicationContext.Consumer>
    )
}



export const Register = ({ redirectPath, onRegister, vendor, loginPath }: { loginPath: string, vendor?: boolean, onRegister: (a) => Promise<any>, redirectPath: string }) => {
    const [state, updateState] = React.useState({ error: '', email: "", password: "", passwordVerify: '', isLoading: false, showPassword: false, displayName: '', companyName: '' })
    const { addToast } = useToasts()
    const [redirect, setRedirect] = React.useState(false)


    return (
        <ApplicationContext.Consumer>{(ctx) => {
            return (
                <form onSubmit={(ev) => {
                    ev.preventDefault(); ev.stopPropagation();

                    if (state.password !== state.passwordVerify) return updateState({ ...state, error: 'Passwords do not match!' })

                    updateState({ ...state, isLoading: true })

                    onRegister(state).then(() => {
                        addToast('Registration Success!', { appearance: 'success', autoDismiss: true })
                        updateState({ ...state, isLoading: false })
                        return setRedirect(true)
                    })
                        .catch(e => {
                            updateState({ ...state, isLoading: false, error: e.message })
                        })
                }}>
                    {redirect ? <Redirect to={redirectPath} /> : null}

                    <div className="card">
                        <section className="card-content">
                            {state.error ?
                                <div className='notification is-warning'>
                                    <FaExclamationCircle /> {state.error}
                                </div>
                                : null}

                            {vendor ?
                                <div className='field is-horizontal'>
                                    <div className="field-label">
                                        <label className="label">Company Name</label>
                                    </div>
                                    <div className='field-body'>
                                        <div className="field">
                                            <div className="control has-icons-left">
                                                <input required name='companyname' value={state.companyName} onChange={(ev) => updateState({ ...state, companyName: ev.target.value })} className="input" type="text" placeholder="Enter company name" />
                                                <span className="icon is-small is-left">
                                                    <FaBriefcase />
                                                </span>
                                            </div>
                                        </div>
                                    </div>
                                </div> : null}

                            <div className='field is-horizontal'>
                                <div className="field-label">
                                    <label className="label">Name</label>
                                </div>
                                <div className='field-body'>
                                    <div className="field">
                                        <div className="control has-icons-left">
                                            <input required name='name' value={state.displayName} onChange={(ev) => updateState({ ...state, displayName: ev.target.value })} className="input" type="person" placeholder="Enter your fullname" />
                                            <span className="icon is-small is-left">
                                                <FaUser />
                                            </span>
                                        </div>
                                    </div>
                                </div>
                            </div>

                            <div className='field is-horizontal'>
                                <div className="field-label">
                                    <label className="label">Email</label>
                                </div>
                                <div className='field-body'>
                                    <div className="field">
                                        <div className="control has-icons-left">
                                            <input required name='email' value={state.email} onChange={(ev) => updateState({ ...state, email: ev.target.value })} className="input" type="email" placeholder="Enter email address" />
                                            <span className="icon is-small is-left">
                                                <FaEnvelope />
                                            </span>
                                        </div>
                                    </div>
                                </div>
                            </div>

                            <div className='field is-horizontal'>
                                <div className="field-label">
                                    <label className="label">Password</label>
                                </div>
                                <div className='field-body'>
                                    <div className="field has-addons">
                                        <p className="control is-expanded has-icons-left">
                                            <input required name='password' value={state.password} onChange={(ev) => updateState({ ...state, password: ev.target.value })} className="input" type={state.showPassword ? 'text' : 'password'} />
                                            <span className="icon is-small is-left">
                                                <FaKey />
                                            </span>
                                        </p>
                                        <p className="control">
                                            <button disabled={state.isLoading} tabIndex={-1} className="button" type="button" onClick={() => updateState({ ...state, showPassword: !state.showPassword })}>{state.showPassword ? <FaEyeSlash /> : <FaEye />}</button>
                                        </p>
                                    </div>
                                </div>
                            </div>

                            <div className='field is-horizontal'>
                                <div className="field-label">
                                    <label className="label">Confirm Password</label>
                                </div>
                                <div className='field-body'>

                                    <div className='field'>
                                        <p className="control has-icons-left">
                                            <input required name='password' value={state.passwordVerify} onChange={(ev) => updateState({ ...state, passwordVerify: ev.target.value })} className="input" type={state.showPassword ? 'text' : 'password'} />
                                            <span className="icon is-small is-left">
                                                <FaKey />
                                            </span>
                                        </p>
                                    </div>
                                </div>
                            </div>

                            <div className='field'>
                                <div className='control'>
                                    <button type='submit' className={`is-success button ${state.isLoading ? 'is-loading' : ''}`} disabled={state.isLoading}>Join</button>
                                </div>
                            </div>

                        </section>

                        <footer className="card-footer">
                            <NavLink to={loginPath} exact className="card-footer-item">Login with existing account</NavLink>
                        </footer>
                    </div>
                </form>
            )
        }}
        </ApplicationContext.Consumer>
    )
}