import React, { useContext, useState, useEffect } from "react";
import { ApplicationContext } from "../lib";
import { Deal } from "../lib/deals";
import { useParams, useLocation, Redirect, useHistory } from "react-router";
import { LoadingComponent } from "./loading";
import { ErrorPage } from "./error";
import Toolbar from "../components/toolbar";
import validator from "validator";
import { GiVendingMachine } from "react-icons/gi";
import { useToasts } from "react-toast-notifications";
import { FaCloud, FaStore, FaClock, FaInfoCircle } from "react-icons/fa";
import { Twitter, Mail, Whatsapp } from "react-social-sharing";
import moment, { Moment } from "moment";
import { FiTrash, FiCopy } from "react-icons/fi";
import { analytics } from "firebase";
import { myAnalytics } from "../lib/firebase";
import { User } from "../lib/user";


export const DealDetails = () => {
    const ctx = useContext(ApplicationContext)
    const { id: dealID } = useParams()
    const { state: locationState } = useLocation()
    const isAllowed = ctx.user && !ctx.user.isAnonymous
    const [state, setState] = useState({
        loading: locationState && locationState.deal ? false : true,
        error: isAllowed ? "" : "You are not authenticated! Please login to view details!",
        deal: isAllowed && locationState ? locationState.deal : null
    })

    useEffect(() => {
        if (!isAllowed) return

        if (state.deal) {
            // if deal is already resolved, do not take any action in effect :)!
            return
        }

        if (!dealID) {
            setState({ ...state, loading: false, error: "Deal could not be found!" })
        }
        else {
            ctx.dealMan.getDeal(dealID).then(deal => {
                setState({ ...state, loading: false, deal, error: "" })
            }).catch(e => setState({ ...state, error: e.message, loading: false, deal: null }))
        }
        return
    }, [dealID, isAllowed, state, ctx.dealMan, ctx.user])

    return (
        state.loading ? (
            <div style={ctx.theme.main}>
                <Toolbar />
                <div className='container'>
                    <div className='section'>

                        <LoadingComponent phrase='Fetching details' />

                    </div>
                </div>
            </div>
        ) : state.error || !state.deal ? (
            <ErrorPage>{state.error}</ErrorPage>
        ) : (
                    <DealContents deal={state.deal} />
                )
    )
}

const DealContents = ({ deal }: { deal: null | Deal }) => {
    const ctx = useContext(ApplicationContext)
    const { addToast } = useToasts()
    const { goBack } = useHistory()
    const [state, setState] = useState({ loading: false })

    if (!deal) throw new Error("Dunno how this happened!!!!")

    if (ctx.isStudent()) {
        const stud = ctx.user as User
        if (!stud.dealExists(deal.id)) {
            if (window.confirm('Want to this deal?')) {
                ctx.dealMan.saveDeal(deal)
            }
            else goBack()
        }
    }
    myAnalytics.logEvent('view_item', { item: deal.id })

    let expiryDate: Moment | undefined = deal.meta && deal.meta.expiryDate ? moment(new Date(deal.meta.expiryDate)) : undefined

    if (deal.meta && deal.meta.actionUrl && validator.isURL(deal.meta.actionUrl, { protocols: ["https"], require_protocol: true })) {
        return (
            <div style={{ ...ctx.theme.main, display: 'flex' }}>
                <Toolbar />
                <div className='content' style={{ flex: 1 }}>
                    <iframe src={deal.meta.actionUrl} title={deal.title} seamless style={{ border: 'none', width: '100%', height: '100%' }} />
                </div>
            </div>
        )
    }

    return (

        <div style={ctx.theme.main}>
            <Toolbar />
            <figure className="image is-unselectable is-16by9">
                <img src={deal.cover || require('../logo.svg')} alt={deal.description} />
            </figure>
            <div className="container is-unselectable has-text-centered">
                <div className='section'>
                    <div className='content'>
                        {deal.meta ?
                            <div className='title is-size-2 has-text-weight-bold'>
                                {validator.equals(deal.meta!.unit, 'FLAT') ? (
                                    `\u20A6 ${deal.value.toLocaleString()}`
                                ) : `${deal.value}%`}
                            </div>
                            : null}
                        <i></i>
                        <p className="subtitle is-centered tags">
                            <span className={`tag ${deal.inStore ? 'is-primary' : 'is-link'}`}>{deal.inStore ? <><FaStore />&nbsp; instore</> : <><FaCloud />&nbsp; online</>}</span>
                            {expiryDate ?
                                <span className={`tag subtitle ${expiryDate.isBefore(Date.now()) ? 'is-danger' : 'is-warning'}`} title={`Deal set to expire on ${expiryDate.toLocaleString()}`}><FaClock />&nbsp; {expiryDate.isBefore(Date.now()) ? 'Expired' : `Expiring ${expiryDate.calendar()}`}</span>
                                : null}
                        </p>
                    </div>
                    <div className='section'>
                        <p className='title is-4'>{deal.title}</p>
                        <p className='subtitle'>{deal.description}</p>

                        <div className="level is-mobile">
                            <div className="level-item">
                                <div>
                                    <p className='heading is-uppercase'><GiVendingMachine /> Company name</p>
                                    <p className="subtitle">{deal.author.CompanyName}</p>
                                </div>
                            </div>
                        </div>
                        <div className="content">
                            {deal.meta && deal.meta.actionUrl ?
                                <>
                                    <div className='field has-addons has-addons-centered'>
                                        <div className='control'>
                                            <input className='input is-uppercase is-medium' id='actionUrl' readOnly type='text' value={deal.meta?.actionUrl} />
                                        </div>
                                        <div title='Copy code' className='control'>
                                            <button className='button is-medium' onClick={() => {
                                                const input: HTMLInputElement | any = document.getElementById('actionUrl')
                                                if (input) {
                                                    input.focus()
                                                    input.select()
                                                    if (navigator.clipboard) navigator.clipboard.writeText(input.value).then(() => addToast('Copied!', { appearance: 'success', autoDismiss: true }))
                                                    else if (document.execCommand('copy', true, input.value)) addToast('Copied!', { appearance: 'success', autoDismiss: true })
                                                }
                                            }}><FiCopy /></button>
                                        </div>
                                        {ctx.user?.isVendor && deal.author.id === ctx.user.uid ? (
                                            <div title='Delete deal' className='control'>
                                                <button className={`button is-medium is-danger ${state.loading ? 'is-loading' : ''}`} onClick={async () => {
                                                    if (!window.confirm(`You have tried to delete deal with title "${deal.title}".\r\nDo you want to delete this deal?`)) return

                                                    setState({ loading: true })
                                                    ctx.dealMan.removeDeal(deal).then(() => addToast("Deal successfully removed!", { appearance: 'success', autoDismiss: true })).catch(e => {
                                                        addToast(e.message || "An error occurred!", { appearance: 'error', autoDismiss: true })
                                                    }).finally(() => setState({ loading: true }))
                                                }}><FiTrash /></button>
                                            </div>
                                        ) : null}
                                    </div>
                                </>
                                : null}
                        </div>
                    </div>
                    <Twitter link={window.location.href} message={deal.title} />
                    <Mail link={window.location.href} subject={deal.title} />
                    <Whatsapp link={window.location.href} message={deal.title} />
                </div>
            </div>
        </div>
    )
}
