import React from 'react'
import { ApplicationContext } from '../lib'
import Toolbar from '../components/toolbar'
import { Footer } from '../components/footer'
import { Login } from '../components/auth'

export const LoginUser = () => {
    return (
        <ApplicationContext.Consumer>{(ctx) => {
            return (
                <div style={ctx.theme.main}>
                    <Toolbar />
                    <div className='section container'>
                        <h2 className='is-title is-size-2 has-text-centered'>Login with username and password</h2>

                        <div className='section'>
                            <Login onLogin={(props) => {
                                return ctx.loginStudent(props)
                            }} redirectPath="/" registerPath={'/user/register'} />
                        </div>
                    </div>
                </div>
            )
        }}
        </ApplicationContext.Consumer>
    )
}

export const LoginVendor = () => {
    return (
        <ApplicationContext.Consumer>{(ctx) => {
            return (
                <div style={ctx.theme.main}>
                    <Toolbar />
                    <div className='section container'>
                        <h2 className='is-title is-size-2 has-text-centered'>Login with username and password</h2>

                        <div className='section'>
                            <Login onLogin={props => { return ctx.loginVendor(props) }} redirectPath="/" registerPath={'/vendor/register'} />
                        </div>
                    </div>
                </div>
            )
        }}
        </ApplicationContext.Consumer>
    )
}