import React, { useContext } from 'react';
import { FaHome, FaSignInAlt, FaSign, FaBriefcase, FaWallet, FaSignOutAlt, FaShoppingBasket, FaTags, FaUserCircle } from "react-icons/fa";
import { NavLink } from 'react-router-dom';
import { ApplicationContext } from '../lib';
import { User, Vendor } from '../lib/user';
import links from '../lib/links';

export default function Toolbar({ hideMenu }: { hideMenu?: boolean }) {
    const ctx = useContext(ApplicationContext)
    let [isActive, updateActive] = React.useState(false)

    if (hideMenu) {
        return (
            <nav className={`navbar is-centered is-unselectable is-fixed-top is-black`} style={ctx.theme.header} >
                <div className="navbar-brand">
                    <span className="navbar-item has-text-weight-bold">{ctx.name}</span>
                </div>
            </nav>
        )
    }

    return (
        <nav className={`navbar is-centered is-unselectable is-fixed-top is-black`} style={ctx.theme.header} >
            <div className="navbar-brand">
                <span className="navbar-item has-text-weight-bold">{ctx.name}</span>
                <span onClick={() => {
                    const isActiveNow = !isActive
                    updateActive(isActiveNow)
                    if (isActiveNow === true) {
                        setTimeout(() => updateActive(false), 10000)
                    }
                }} className={`navbar-burger burger ${isActive ? 'is-active' : ''}`}>
                    <span></span>
                    <span></span>
                    <span></span>
                </span>
            </div>
            <div className={`navbar-menu ${isActive ? 'is-active' : ''}`}>
                <div className="navbar-start">
                    {ctx.signedIn() ?
                        <NavLink activeClassName='is-active' exact to={links.myDeals} className='navbar-item'>
                            <FaWallet />&emsp;My Deals
                </NavLink> :
                        <NavLink activeClassName='is-active' exact to='/' className='navbar-item'>
                            <FaHome />&emsp;Home
          </NavLink>}
                    <NavLink activeClassName='is-active' exact to={links.deals} className='navbar-item'>
                        <FaTags />&nbsp; Deals
                </NavLink>
                </div>
                <div className="navbar-end">
                    {ctx.signedIn() ?
                        <>
                            <div className="navbar-item">
                            </div>
                            <NavLink title="View profile" activeClassName='is-active' exact to={links.profile} className='navbar-item'>
                                <FaUserCircle />&emsp;Hello {ctx.user!.isVendor() ? (ctx.user as Vendor)!.companyName : ctx.user!.displayName}
                                </NavLink>
                        </> : null}
                    <div className="navbar-item">
                        {!ctx.signedIn() ?
                            <div className="field is-grouped is-uppercase">
                                <p className="control">
                                    <NavLink title='Login to student portal' activeClassName='is-active' exact to='/user/login' className="is-primary button">
                                        <FaSignInAlt />&emsp;Student
                                        </NavLink>
                                </p>
                                <p className="control">
                                    <NavLink title="Login to vendor portal!" activeClassName='is-active' exact to='/vendor/login' className="is-link button">
                                        <FaBriefcase />&emsp;Vendor
                                        </NavLink>
                                </p>
                            </div> :
                            <NavLink title='Logout' activeClassName='is-active' exact to={`/${ctx.user!.isVendor() ? 'vendor' : 'user'}/logout`} className="is-danger button">
                                <FaSignOutAlt />&emsp;Logout
                            </NavLink>
                        }
                    </div>
                </div>
            </div>
        </nav>
    )
}