import React from "react";
import { FaExclamationTriangle } from "react-icons/fa";
import { NavLink } from "react-router-dom";
import Toolbar from "../components/toolbar";
import { ApplicationContext } from "../lib";

export function ErrorPage(props: { children: any, redirect?: string, redirectText?: string }) {
    return (
        <ApplicationContext.Consumer>{(ctx) => {
            return (
                <div style={ctx.theme.main}>
                    <Toolbar />
                    <div className='hero is-black is-bold is-fullheight'>
                        <div className='hero-body'>
                            <div className='container notification has-text-centered' style={ctx.theme.level1}>
                                <FaExclamationTriangle size={50} className="has-text-warning" />
                                <p className='title is-uppercase'> {props.children || "Page Not Found!"}</p>
                                <br />
                                <br />
                                <NavLink to={props.redirect || '/'} className='button is-danger is-outlined'>{props.redirectText || 'Go to Homepage!'}</NavLink>
                            </div>
                        </div>
                    </div>
                </div>
            )
        }}
        </ApplicationContext.Consumer>
    )
}