import { Context } from "react"
import { AppContext } from "."
import { dealsRef, libraryRef, vendorsRef } from "./user"
import validator from "validator";
import { functions, storage, analytics } from "firebase";
import moment from "moment";
import { myAnalytics } from "./firebase";

export class DealManager {
    protected context: AppContext

    /**
     * Use mock data in application deal management.
     * 
     * @internal Only used for testing.
     */
    protected useMock: boolean = false

    constructor(ctx: AppContext) {
        if (process.env.NODE_ENV === 'production' && this.useMock) {
            console.info('Bail application. Wrong code sent for production!!!!!!')
            // throw new Error('Production environment should not use mock data!')
        }

        this.context = ctx
        // @ts-ignore
        window.v = validator
    }

    private async inflateDeal(deal: Deal) {
        if (!deal.author.CompanyName) {
            const nameRef = vendorsRef.child(`${deal.author.id}/companyName`)
            const name = await nameRef.once('value').then(async s => {
                if (s.exists()) {
                    return s.val()
                }
            })
            deal.author.CompanyName = name
        }
        return deal
    }

    async getDeal(id) {
        return dealsRef.child(id).once('value').then(v => {
            if (v.exists()) {
                const deal = this.inflateDeal(v.val())
                return deal
            } else throw new Error("Deal not found!")
        })
    }

    async getMyDeals() {
        if (!this.context.signedIn()) return []

        if (this.useMock) return MOCK_DEALS

        return await new Promise(res => {
            libraryRef.child(`${this.context.user!.uid}`).once('value', async snapshot => {
                if (!snapshot.exists()) return res([])

                const deals = snapshot.val()
                console.log(deals)
                const result: Deal[] = []

                for (let c in deals) {
                    const item = deals[c]
                    result.push(await this.inflateDeal(item))
                }

                return res(result.reverse())
            })
        }
        )
    }

    async getDeals(startAt?: string) {
        const dealsPerPage = 10

        if (startAt) {
            if (this.useMock) {
                const lastItemIndex = MOCK_DEALS.findIndex(item => item.id === startAt)
                console.log(lastItemIndex, startAt, MOCK_DEALS)
                return MOCK_DEALS.slice(lastItemIndex > 0 ? lastItemIndex + 1 : 0, (Math.max(lastItemIndex, 0) + 1) + dealsPerPage)
            }

            return await dealsRef.orderByKey().endAt(startAt).limitToLast(2).once('value').then(async snapshot => {
                if (!snapshot.exists()) return []

                const deals = snapshot.val()
                console.log('all deals', deals)
                const result: Deal[] = []

                for (let c in deals) {
                    if (c === startAt) continue

                    const item = deals[c]
                    result.push(await this.inflateDeal(item))
                }

                return result.reverse()
            })
        }

        if (this.useMock) return MOCK_DEALS.slice(0, dealsPerPage)

        return await dealsRef.orderByKey().limitToLast(dealsPerPage).once('value').then(async snapshot => {
            if (!snapshot.exists()) return []

            const deals = snapshot.val()
            const result: Deal[] = []

            for (let c in deals) {
                const item = deals[c]
                if (item.expiry && Date.now() >= item.expiry) continue

                result.push(await this.inflateDeal(item))
            }

            return result.reverse()
        })
    }

    async addDeal(deal: Deal) {
        if (!this.context.signedIn() || !this.context.user!.isVendor()) {
            throw new Error("You are not authorized to create deals!")
        }

        if (!deal.cover) {
            throw new Error("You must specify a cover image for this deal!")
        }

        const createDeal = functions().httpsCallable('vendorCreateDeal')
        DealManager.isDealValid(deal)

        try {
            const result = await createDeal({ deal })
            if (!result.data.success) throw new Error("Failed to create deal!")
            myAnalytics.logEvent('create_deal')
            return true
        } catch (e) {
            console.error(e)
            throw e
        }
    }

    async saveDeal(deal: Deal) {
        if (!this.context.isStudent()) {
            throw new Error("Only students can save deals!")
        }

        try {
            DealManager.isDealValid(deal)
        } catch (e) {
            throw new Error("Deal is inproperly formatted!")
        }
        try {
            await this.context.user?.saveDeal(deal)
            return true
        } catch (e) {
            console.error(e)
            throw e
        }
    }

    async removeDeal(deal: Deal) {
        if (!this.context.signedIn() || !this.context.user!.isVendor() || deal.author.id !== this.context.user?.uid) {
            throw new Error("You are not authorized to manage deals!")
        }

        try {
            if (deal.cover) await storage().refFromURL(deal.cover).delete()
            await libraryRef.child(`${this.context.user.uid}/${deal.id}`).remove()
            await dealsRef.child(deal.id).remove()
            return true
        } catch (e) {
            console.error(e)
            throw e
        }
    }


    static isDealValid(deal: Deal) {

        if (!deal.title || !validator.isLength(deal.title, { min: 5 })) {
            throw Error("Deal must contain a useful title!")
        }
        else if (!deal.description || !validator.isLength(deal.description, { min: 10 })) {
            throw new Error("Deal must have useful description!")
        }
        else if (!deal.value) {
            throw new Error("All deals must have a value!")
        }

        deal.meta = deal.meta || { unit: "FLAT" }

        if (!deal.meta.unit || (!validator.equals(deal.meta.unit, "PERCENT") && !validator.equals(deal.meta.unit, "FLAT"))) {
            throw new Error("Invalid unit format")
        }
        // if (deal.meta.actionUrl && !validator.isURL(deal.meta.actionUrl, { protocols: ["http", "https"] })) {
        //     throw new Error("Url provided is invalid")
        // }
        if (deal.meta.expiryDate && !moment(deal.meta.expiryDate).isAfter()) {
            throw new Error("Invalid expiry date!")
        }

        return true
    }
}

/**
 * ValueType:
 * Discount | The deal provides a difference from the regular price.
 * This discount may be in the form of percentage removal from the current price of a product.
 * It could also represent a flat amount removed from the purchase price.
 * The type of discount is specified by the `ValueUnit` property.
 */
export type ValueUnit = "PERCENT" | "FLAT"

export interface Deal {
    title: string,
    inStore?: boolean,
    description: string,
    value: number,
    meta?: {
        actionUrl?: string,
        unit: ValueUnit,
        expiryDate?: number | Date
    }
    cover?: string,
    author: {
        id: string,
        CompanyName?: string,
        photoURL?: string
    }
    id: string
}


