import React, { useContext, useState } from 'react'
import { ApplicationContext } from '../lib'
import Toolbar from '../components/toolbar'
import { Footer } from '../components/footer'
import { Login, Register } from '../components/auth'
import { Redirect } from 'react-router'

export const Logout = () => {
    const ctx = useContext(ApplicationContext)
    const [loading, setLoading] = useState(true)

    if (!ctx.signedIn()) {
        // return <Redirect to='/' />
    }

    ctx.logout().then(() => setLoading(false))

    return (
        <div style={ctx.theme.main}>
            <Toolbar />
            <div className='section container'>

                <div className='section'>
                    <div className='hero is-fullheight'>
                        <div className='hero-body'>
                            <h2 className='is-title is-size-2'>Thank you for using {ctx.name}!</h2>
                            {loading ? <progress className="progress is-small is-info" max="100">loading...</progress> : null}
                        </div>
                    </div>
                </div>
            </div>
        </div>

    )
}
