import firebase from "firebase"
import * as fBase from './firebase'
import { log } from "./util"
import { Deal } from "./deals"

const functions = firebase.functions()
export const auth = firebase.auth()
export const usersRef = firebase.database().ref('users')
export const vendorsRef = firebase.database().ref('vendors')
export const dealsRef = firebase.database().ref('deals')
export const libraryRef = firebase.database().ref('library')

export class User {
    uid: string
    displayName: string
    protected fUser: firebase.User
    readonly isAnonymous: boolean = false
    protected myDeals: string[] = []

    constructor(userObj: firebase.User) {
        this.uid = userObj.uid
        this.displayName = userObj.displayName ? userObj.displayName : ""
        this.fUser = userObj
        this.isAnonymous = this.fUser.isAnonymous
        if (!this.isAnonymous) {
            fetch(`${fBase.firebaseConfig.databaseURL}/library/${this.uid}.json?shallow=true`).then(res => res.json()).then(data => {
                this.myDeals = Object.keys(data)
            })
        }
    }

    isVendor(): boolean {
        return false
    }

    dealExists(id: string) {
        return this.myDeals.find(v => v === id) || false
    }

    async saveDeal(deal: Deal) {
        if (this.dealExists(deal.id)) return

        await libraryRef.child(`${this.uid}/${deal.id}`).set(deal)
        this.myDeals.push(deal.id)
    }
    /**
     * All access to app is anonymous by default.
     * 
     * Upon registration, the user account is linked to the current aonymous user.
     */
    static async register({ displayName, email, password }: { email: string, password: string, displayName: string }) {
        if (!auth.currentUser) {
            auth.signInAnonymously()
            throw new Error("This is weird! Application must start with atleast an anonymous user!")
        }

        const credential = firebase.auth.EmailAuthProvider.credential(email, password)
        return auth.currentUser.linkWithCredential(credential).then(async (usercred) => {
            if (!usercred.user) {
                throw new Error("User should not be empty!")
            }

            const user = usercred.user;
            console.log("Anonymous account successfully upgraded", user);

            await user.updateProfile({ displayName }).then(() => usersRef.child(user.uid).set({
                displayName,
                email
            })
            ).catch(e => {
                log(e)
                user!.delete()
                throw new Error("Could not register user!")
            })

            return new User(user)
        }, (error) => {
            console.log('Error upgrading anonymous account', error)
            throw new Error(error.message);
        })
    }

    static async login(email: string, password: string) {
        return auth.signInWithEmailAndPassword(email, password)
            .then(cred => {
                if (!cred.user) throw new Error("User not found!")
                return new User(cred.user)
            })
            .catch((error) => {
                // Handle Errors here.
                var errorCode = error.code;

                console.log(error)
                if (errorCode === 'auth/wrong-password') {
                    throw new Error('Wrong password.');
                } else {
                    throw error;
                }
            })
    }
}

/**
 * Vendor is an organization with access to publish and manage deals.
 */
export class Vendor extends User {
    companyName: string

    constructor(userObj: firebase.User, companyName: string) {
        super(userObj)
        this.companyName = companyName
    }

    isVendor() {
        return true
    }

    /**`
     * Registers a new vendor.
     * 
     * Vendor registration is different from user registration and only one account type can be signedin at a time.
     */
    static async register({ companyName, displayName, email, password }: { companyName: string, email: string, password: string, displayName: string }) {
        if (!auth.currentUser) {
            auth.signInAnonymously()
            throw new Error("This is weird! Application must start with atleast an anonymous user!")
        }

        const register = functions.httpsCallable('vendorSignup')

        try {
            const result = await register({ email, password, displayName, companyName })
            console.log(result)

            if (!result.data.success) throw new Error('Something went wrong!')

            const cred = await auth.signInWithEmailAndPassword(email, password)
            if (!cred.user) throw new Error('Something went horribly wrong!')

            return new Vendor(cred.user, companyName)
        } catch (e) {
            console.error(e)
            throw e
        }
    }

    /**
     * Login authenticates the current user as a vendor.
     * 
     * If the company name of this vendor account is not found an error is thrown.
     * It is an invalid action to create vendors without populating the company name.
     */
    static async login(email: string, password: string) {
        return auth.signInWithEmailAndPassword(email, password)
            .then(async cred => {
                if (!cred.user) throw new Error("User not found!")

                const companyName: string = await new Promise((res, rej) => {
                    vendorsRef.child(`${cred.user!.uid}/companyName`).once('value', (snapshot) => {
                        if (snapshot.exists()) {
                            return res(snapshot.val())
                        }
                        //Vendor cannot exist without having a company name!
                        auth.signOut()
                        rej(new Error('Vendor account not found! Did you mean to login as a user?'))
                    })
                })
                return new Vendor(cred.user, companyName)
            })
            .catch((error) => {
                // Handle Errors here.
                var errorCode = error.code;

                console.log(error)
                if (errorCode === 'auth/wrong-password') {
                    throw new Error('Wrong password.');
                } else {
                    throw error;
                }
            })
    }
}