import { createContext } from 'react'
import * as fBase from './firebase'
import { User, Vendor, auth } from './user'
import { log } from './util'
import { DEFAULT_THEME } from "./theme";
import firebase, { analytics } from 'firebase';
import { DealManager } from './deals';

export const USER_SESSION = 'user_session_'

export class AppContext {
    // App datails
    name = "My Student Deal"
    description = "Bringing the most exciting shopping discounts and deals to students worldwide!"
    version = "0.0.1"

    // Theme settings
    theme = DEFAULT_THEME

    // Instance properties
    //
    // Currently logged in user
    user?: User
    dealMan: DealManager

    protected _isReady: Promise<boolean>

    constructor() {
        document.title = this.name
        this.dealMan = new DealManager(this)

        this._isReady = new Promise((res) => {
            auth.onAuthStateChanged(async user => {
                if (user) {
                    // If user is anonymous, create a user object to allow saving state once activity is done.
                    if (user.isAnonymous) {
                        console.log("Anonymous user created", user)
                        this.user = new User(user)
                    }
                    else {
                        // If user is already created, skip completely.
                        // This function is for inflating state upon reload and should not be run within a page view.
                        if (this.user && !this.user.isAnonymous) return

                        // get custom claims
                        const token = await user.getIdTokenResult()
                        if (token.claims.vendor) {
                            // if (true) {
                            const session = JSON.parse(localStorage.getItem(USER_SESSION) || "{}")
                            if (!session) {
                                // dunno how you authenticated without a session.
                                auth.signOut()
                                console.log("Anonymous user created", user)
                                this.user = new User(user)
                            }
                            console.log("currently signedin as ", session.companyName, user)
                            this.user = new Vendor(user, session.companyName)
                            fBase.myAnalytics.setUserId(user.uid)
                        } else {
                            console.log("currently signedin as ", user.displayName, user)
                            this.user = new User(user)
                            fBase.myAnalytics.setUserId(user.uid)
                        }
                    }

                }
                else {
                    this.user = undefined
                    // Signs in current user anonymously
                    if (!this.user) auth.signInAnonymously()
                }
                res(true)
            })
        })

    }

    isReady() {
        return this._isReady
    }

    isStudent() {
        return this.user && this.signedIn() && !this.user.isVendor()
    }

    signedIn() {
        return this.user && !this.user.isAnonymous
    }

    loginStudent(userDetails: { email: string, password: string }) {
        return User.login(userDetails.email, userDetails.password).then(user => {
            this.user = user
            return this
        })
    }

    loginVendor(vendorDetails: { email: string, password: string }) {
        return Vendor.login(vendorDetails.email, vendorDetails.password).then(user => {
            this.user = user
            localStorage.setItem(USER_SESSION, JSON.stringify({ companyName: user.companyName }))
            return this
        })
    }


    registerUser(userDetails: { displayName: string, email: string, password: string }) {
        return User.register(userDetails).then(user => {
            this.user = user
        })
    }

    registerVendor(vendorDetails: { displayName: string, email: string, password: string, companyName: string }) {
        return Vendor.register(vendorDetails).then(user => {
            this.user = user
            localStorage.setItem(USER_SESSION, JSON.stringify({ companyName: user.companyName }))
        })
    }

    async logout() {
        await auth.signOut()
        localStorage.removeItem(USER_SESSION)

        if (this.user) {
            this.user = undefined
        }
    }


    async getVendorCount() {
        const vendors = await (await fetch(`${fBase.firebaseConfig.databaseURL}/vendors.json?shallow=true`)).json()
        if (!vendors) return 0
        return Object.keys(vendors).length
    }
}

export const DefaultAppContext = new AppContext()
export const ApplicationContext = createContext<AppContext>(DefaultAppContext)
