export interface Theme {
    /**
     * Main is the lowest level of the screen. This is the reall background of the application.
     */
    main?: React.CSSProperties,
    mainDark?: React.CSSProperties,
    level1?: React.CSSProperties
    header?: React.CSSProperties
    footer?: React.CSSProperties
}

export const DEFAULT_THEME: Theme = {
    main: {
        background: "#fcfcfc",
        color: "#002",
        minHeight: '100%',
        overflowY: 'scroll',
        paddingTop: '3.25rem'
    },
    mainDark: {
        background: "#001",
        color: "#efefef",
        minHeight: '100%',
        overflowY: 'scroll',
        paddingTop: '3.25rem'
    },
    level1: {
        background: "#222"
    },
    header: {
        background: "#353545",
        color: '#fefefe',
        boxShadow: '#1a1a1a88 0 1px 0.1em 0.1em'
    },
    footer: {
        padding: '1em',
        color: '#eee',
        background: '#444455'
    }
}