import React, { useContext, useEffect, useState } from 'react'
import Toolbar from '../components/toolbar'
import { ApplicationContext } from '../lib'


const LoadingPhrases: string[] = ['Contacting Mothership...', 'Configuring Defences...', 'Loading...', 'Ready In A Minute...', 'Launching...']

export const Loading = () => {
    const ctx = useContext(ApplicationContext)
    // const [loading, setLoading] = React.useState<boolean>(true)
    const [phrase, setPhrase] = useState<string>(LoadingPhrases[Math.round(Math.random() * LoadingPhrases.length) % LoadingPhrases.length])

    let intID = setInterval(() => {
        if (intID) clearInterval(intID)
        setPhrase(LoadingPhrases[Math.round(Math.random() * LoadingPhrases.length) % LoadingPhrases.length])
    }, 2000)
    useEffect(() => {
        return () => {
            clearInterval(intID)
        }
    })

    return (
        <div style={ctx.theme.main}>
            <Toolbar hideMenu />
            <div className='hero is-fullheight is-black is-bold'>
                <div className='hero-body has-text-centered'>
                    <div className='container'>
                        <p className='title has-text-weight-bold is-2'>Welcome to {ctx.name}</p>
                        <p className='subtitle'>{ctx.description}</p>
                        <br />
                        <br />
                        <div className='section'>
                            <p className='subtitle'>{phrase}</p>
                            <progress className="progress is-small is-info" max="100">loading</progress>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    )
}


const types = ['info', 'success', 'link', 'warning', 'danger', 'primary']
export const LoadingComponent = ({ phrase = "Loading..." }) => {
    const [type, setType] = useState<string>('info')

    useEffect(() => {
        const intervalID = setInterval(() => setType(types[Math.round(Math.random() * types.length) % types.length]),900)
        return () => clearInterval(intervalID)
    })

    return (
        <div className='container has-text-centered'>
            <p className='subtitle'>{phrase}</p>
            <progress className={`progress is-small is-${type}`} max="100">loading</progress>
        </div>
    )
}