import React, { useContext } from 'react'
import { ApplicationContext } from '../lib'
import Toolbar from '../components/toolbar'
import { Footer } from '../components/footer'
import { GiShirt, GiSmartphone, GiCityCar, GiGlassCelebration, GiVendingMachine } from 'react-icons/gi'
import { NavLink, Redirect } from 'react-router-dom'
import links from '../lib/links'
import { useIncreaseNumber } from '../lib/hooks'

export const Home = () => {
    const ctx = useContext(ApplicationContext)
    const [current, update] = React.useState<number>(0)

    if (ctx.signedIn()) {
        return <Redirect to={links.myDeals} />
    }

    const startCount = (destination, step = 10) => {
        const intervalID = setInterval(() => {
            if (destination !== current) {
                const newVal = current + step
                if (newVal > destination) {
                    update(destination)
                    clearInterval(intervalID)
                } else {
                    update(newVal)
                }
            } else {
                clearInterval(intervalID)
            }
        }, Math.max(Math.ceil(destination / step)), 0)
    }

    ctx.getVendorCount().then((d) => startCount(d))

    return (
        <div style={ctx.theme.main}>
            <Toolbar />
            <div className='hero is-fullheight is-light is-bold'>
                <div className='hero-body has-text-centered has-background'>
                    <div className='container'>
                        <p className='title has-text-weight-bold is-2'>Welcome to {ctx.name}</p>
                        <p className='subtitle'>{ctx.description}</p>
                    </div>
                </div>
            </div>
            <div className='hero is-medium is-primary is-bold'>
                <div className='hero-body has-text-centered'>
                    <div className='container'>
                        <p className='title is-capitalized'>
                            Gain access to thousands of great student discounts!
                                </p>
                        <div className='tile is-ancestor section'>
                            <NavLink to={links.Fashion} className='tile is-parent'>
                                <div className='tile is-child box'>
                                    <GiShirt className='is-size-1' />
                                    <p className='subtitle has-text-black has-text-weight-bold'>Fashion</p>
                                </div>
                            </NavLink>
                            <NavLink to={links.Technology} className='tile is-parent'>
                                <div className='tile is-child box'>
                                    <GiSmartphone className='is-size-1' />
                                    <p className='subtitle has-text-black has-text-weight-bold'>Technology</p>
                                </div>
                            </NavLink>
                            <NavLink to={links.Transport} className='tile is-parent'>
                                <div className='tile is-child box'>
                                    <GiCityCar className='is-size-1' />
                                    <p className='subtitle has-text-black has-text-weight-bold'>Transportation</p>
                                </div>
                            </NavLink>
                            <NavLink to={links.LifeStyle} className='tile is-parent'>
                                <div className='tile is-child box'>
                                    <GiGlassCelebration className='is-size-1' />
                                    <p className='subtitle has-text-black has-text-weight-bold'>Events &amp; Lifestyle</p>
                                </div>
                            </NavLink>
                        </div>
                    </div>
                </div>
            </div>
            <div className='hero is-medium is-dark'>
                <div className='hero-body has-text-centered'>
                    <div className='container'>
                        <GiVendingMachine className='is-size-1' />
                        <p className='subtitle'>Vendors Registered</p>
                        <p className='title has-text-weight-bold is-1'>{current}
                        </p>
                    </div>
                </div>
            </div>
        </div>
    )
}