import React from 'react'
import { ApplicationContext } from '../lib'
import Toolbar from '../components/toolbar'
import { Footer } from '../components/footer'
import { Login, Register } from '../components/auth'

export const RegisterUser = () => {
    return (
        <ApplicationContext.Consumer>{(ctx) => {
            return (
                <div style={ctx.theme.main}>
                    <Toolbar />
                    <div className='section container'>
                        <h2 className='is-title is-size-2 has-text-centered'>Create a new account</h2>

                        <div className='section'>
                            <Register onRegister={props => ctx.registerUser(props)} redirectPath="/" loginPath={'/user/login'} />
                        </div>
                    </div>
                </div>
            )
        }}
        </ApplicationContext.Consumer>
    )
}

export const RegisterVendor = () => {
    return (
        <ApplicationContext.Consumer>{(ctx) => {
            return (
                <div style={ctx.theme.main}>
                    <Toolbar />
                    <div className='section container'>
                        <h2 className='is-title is-size-2 has-text-centered'>Create a new vendor account</h2>

                        <div className='section'>
                            <Register vendor onRegister={props => ctx.registerVendor(props)} redirectPath="/" loginPath={'/vendor/login'} />
                        </div>
                    </div>
                </div>
            )
        }}
        </ApplicationContext.Consumer>
    )
}