import React, { useContext, useState, PureComponent } from 'react'
import { ApplicationContext } from '../lib'
import Toolbar from '../components/toolbar'
import moment, { Moment } from "moment";
import { GiVendingMachine, GiWallet } from 'react-icons/gi'
import { NavLink, Redirect, Prompt } from 'react-router-dom'
import { Deal, DealManager } from '../lib/deals'
import { FaStore, FaCloud, FaExclamationCircle, FaCalendar, FaClock, FaGlobe } from 'react-icons/fa'
import { FiDelete, FiImage, FiFrown } from "react-icons/fi";
import { Vendor } from '../lib/user'
import { storage } from 'firebase'
import { useToasts } from 'react-toast-notifications'
import validator from 'validator'
import { LoadingComponent } from './loading'
import _ from "underscore";


export const DealWidget = ({ user, dealMan }: { user: Vendor, dealMan: DealManager }) => {
    const blank: {
        error: any,
        cover: any
    } & any = {
        error: null,
        inStore: "ONLINE",
        title: "",
        description: "",
        value: "",
        expiryTime: "",
        expiryDate: "",
        cover: null,
        link: '',
        type: 'FLAT',
        loading: false,
        coverFile: null
    }
    const [state, setState] = useState(blank)
    const { addToast } = useToasts()


    return (
        <form className='card has-transition-1 is-unselectable' onSubmit={async (e) => {
            e.preventDefault()
            e.stopPropagation()

            if (!user.isVendor()) {
                addToast('Only vendors can create deals', { appearance: 'error' })
            }

            if (state.loading) {
                addToast("Deal creation already in progress!", { appearance: 'info', autoDismiss: true })
                return
            }

            setState({ ...state, loading: true, error: null })
            // verify all require fields.
            if (state.title && state.title.length > 4 && state.description && state.description.length > 4 && state.cover && state.value) {
                try {
                    if (state.inStore === 'ONLINE' && !state.link) throw new Error("All online deals must have a url or voucher code!")
                    //set expiry
                    let expiry
                    if (state.expiryDate && state.expiryTime) {
                        expiry = new Date(`${state.expiryDate} ${state.expiryTime}`)
                        if (expiry.getTime() <= Date.now()) throw new Error("Expiry must be greater than today!")
                    } else if ((state.expiryTime && !state.expiryDate) || (state.expiryDate && !state.expiryTime)) { throw new Error("Both expiry date and time must be set!") }

                    const deal: Deal = {
                        title: state.title,
                        description: state.description,
                        author: {
                            id: user.uid
                        },
                        id: "",
                        value: state.value,
                        inStore: state.inStore !== 'ONLINE' ? true : false,
                        meta: {
                            unit: state.type,
                            actionUrl: state.link,
                            expiryDate: expiry ? expiry.getTime() : undefined
                        }
                    }

                    DealManager.isDealValid(deal)

                    // upload image
                    const brokenNames: string[] = state.cover.name.split('.')
                    const storeRef = storage().ref(`images/${Date.now().toString(36)}_${btoa(brokenNames[0])}_${brokenNames[brokenNames.length - 1]}`)
                    storeRef.put(state.cover).then(async t => {
                        if (t.state === 'success') deal.cover = await storeRef.getDownloadURL()
                        else {
                            storeRef.delete()
                            console.log(t)
                            throw new Error("Failed to save file properly!")
                        }
                        await dealMan.addDeal(deal).catch(e => {
                            storeRef.delete()
                            throw e
                        })
                        addToast("Deal created successfully!", { appearance: 'success', autoDismiss: true })
                        setState({ ...state, error: "", loading: false })
                    }).catch(e => {
                        if (process.env.NODE_ENV === 'development') console.error(e)
                        setState({ ...state, error: e.message || "Failed to upload. Please try again", loading: false })
                    })
                } catch (e) {
                    if (process.env.NODE_ENV === 'development') console.error(e)
                    setState({ ...state, error: e.message || "Failed to upload. Please try again", loading: false })
                }
            } else {
                setState({
                    ...state, error: "Invalid values entered!", loading: false
                })
            }
        }
        }>
            <Prompt message='You have made changes to this page. Really want to leave now?' when={!_.isEqual(state, blank)} />
            <header className='card-header'>
                <p className='card-header-title is-uppercase'>Create New Deal</p>
                <span onClick={() => {
                    if (state.loading) return
                    setState(blank)
                }} className={`card-header-icon ${state.loading ? 'has-text-grey' : 'has-text-danger is-size-4'}`}> <FiDelete /></span>
            </header>
            {state.coverFile ? (
                <div className="card-image">
                    <figure className="image is-16by9">
                        <img src={state.coverFile} alt={state.description} />
                    </figure>
                </div>)
                : null}
            <div className='card-content' >
                {state.error ?
                    <div className='notification is-danger'>
                        <FaExclamationCircle /> {state.error}
                    </div>
                    : null}
                <div className='field is-horizontal'>
                    <div className='field-body'>
                        <div className='field'>
                            <div className='control'>
                                <input value={state.title} onChange={e => setState({ ...state, title: e.target.value })} tabIndex={1} className='input' required disabled={state.loading} minLength={4} type='text' placeholder='Title' />
                            </div>
                        </div>
                        <div className='field'>
                            <div className={`file has-name is-fullwidth ${state.cover ? 'is-success' : ''}`}>
                                <label className='file-label'>
                                    <input onChange={e => {
                                        if (e.target.files && e.target.files.length > 0 && e.target.files[0].size > 10) {
                                            const file = e.target.files[0]
                                            if (file.size / 1024 > 700) {
                                                addToast("File is too large! Please select a different file!", { appearance: 'error', autoDismiss: true })
                                                return
                                            }
                                            if ('FileReader' in window) {
                                                const reader = new FileReader()
                                                reader.onload = (e) => {
                                                    setState({ ...state, cover: file, coverFile: e.target?.result })
                                                }
                                                reader.readAsDataURL(file)
                                            } else setState({ ...state, cover: file })

                                        } else {
                                            setState({ ...state, cover: null })
                                        }

                                    }} className="file-input" required tabIndex={2} type='file' accept='image/*' alt='Cover photo for deal' disabled={state.loading} />
                                    <span className='file-cta'>
                                        <span className='file-icon'>
                                            <FiImage />
                                        </span>
                                        <span className={`is-uppercase file-label ${state.cover ? 'has-text-white' : 'has-text-grey'}`}>
                                            Select Cover image
                                    </span>
                                    </span>
                                    <span className='file-name'>{state.cover ? state.cover.name : 'no file chosen'}</span>
                                </label>
                            </div>
                        </div>
                    </div>
                </div >

                <div className="field">
                    <div className="control">
                        <textarea tabIndex={3} className="textarea" value={state.description} onChange={e => setState({ ...state, description: e.target.value })} disabled={state.loading} required maxLength={150} rows={2} placeholder="Describe what this deal has to offer..."></textarea>
                    </div>
                </div>

                <div className='field is-horizontal'>
                    <div className='field-body'>
                        <div className='field'>
                            <div className='control' title="Select deal availability. This will guide users on where this deal can be applied.">
                                <div className='select is-fullwidth'>
                                    <select tabIndex={4} value={state.inStore} onChange={e => setState({ ...state, inStore: e.target.value })} disabled={state.loading} >
                                        <option value='ONLINE'>Online</option>
                                        <option value='STORE'>Instore</option>
                                    </select>
                                </div>
                            </div>
                        </div>

                        <div className='field has-addons'>
                            <div className='control'>
                                <div className='select'>
                                    <select tabIndex={5} value={state.type} onChange={e => setState({ ...state, type: e.target.value })} disabled={state.loading} >
                                        <option value="PERCENT">%</option>
                                        <option value="FLAT">&#x20A6;</option>
                                    </select>
                                </div>
                            </div>
                            <div className='control is-expanded'>
                                <input tabIndex={6} value={state.value} onChange={e => setState({ ...state, value: e.target.value })} className='input is-uppercase' disabled={state.loading} required type='number' step={0.01} placeholder='Discount or Percentage' />
                            </div>
                        </div>
                    </div>
                </div>
                {
                    state.inStore !== 'ONLINE' ? null : (
                        <div className='field'>
                            <div className='control has-icons-left'>
                                <input type='text' tabIndex={7} value={state.link} onChange={e => setState({ ...state, link: e.target.value })} disabled={state.loading} className='input' placeholder="Voucher Code or Web URL (starting with http://)" />
                                <span className='icon is-small is-left'>
                                    <FaGlobe />
                                </span>
                            </div>
                        </div>
                    )
                }

                <label className='label is-uppercase has-text-grey'>Expiry (optional)</label>
                <div className='field is-horizontal'>
                    <div className='field-body'>
                        <div className='field'>
                            <div className="control has-icons-left">
                                <input className="input" tabIndex={8} value={state.expiryTime} disabled={state.loading} onChange={e => setState({ ...state, expiryTime: e.target.value })} type="time" placeholder="Time deal will expire" />
                                <span className="icon is-small is-left">
                                    <FaClock />
                                </span>
                            </div>
                        </div>
                        <div className='field'>
                            <div className="control has-icons-left">
                                <input name='date' tabIndex={9} disabled={state.loading} value={state.expiryDate} onChange={e => setState({ ...state, expiryDate: e.target.value })} className="input" type="date" pattern='[0-9]{4}/[0-9]{2}/[0-9]{2}' />
                                <span className="icon is-small is-left">
                                    <FaCalendar />
                                </span>
                            </div>
                        </div>
                    </div>
                </div>
                <div className='field'>
                    <div className='is-fullwidth control'>
                        <button type='submit' tabIndex={10} disabled={state.loading} className={`button is-primary is-uppercase ${state.loading ? 'is-loading' : ''}`}>submit</button>
                    </div>
                </div>
            </div >
        </form >)
}

export class MyDeals extends PureComponent {
    static contextType = ApplicationContext
    context!: React.ContextType<typeof ApplicationContext>

    state: { deals: Deal[], isLoading: boolean } = { deals: [], isLoading: true }

    componentDidMount() {
        this.context.dealMan.getMyDeals().then(deals => {
            this.setState({ deals, isLoading: false })
        })
    }

    render() {
        const ctx = this.context
        const { state: { deals, isLoading } } = this

        if (!ctx.signedIn()) {
            return <Redirect to='/' />
        }

        return (
            <div style={ctx.theme.main}>
                <Toolbar />
                <div className='container'>
                    <div className='section'>
                        {ctx.user?.isVendor() ? <DealWidget user={ctx.user as Vendor} dealMan={ctx.dealMan} /> : null}

                        <div className='section is-marginless-horizontal'>
                            {isLoading ? <LoadingComponent phrase='Fetching Your deals...' /> : null}
                            <div className='columns is-multiline'>
                                {deals.length > 0 ? deals.map(deal => {
                                    return (
                                        <div key={deal.id} className='column is-3-widescreen is-4 is-12-mobile'>
                                            <DealComponent deal={deal} />
                                        </div>
                                    )
                                }) : <div className='column has-text-centered has-text-grey'>
                                        <br />
                                        <FiFrown className='is-size-1' />
                                        <br />
                                        No Data available yet!
                                    </div>}
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        )
    }
}

/**
 * Component for displaying all deals!
 *
 * The deals are paginated and only fetch more deals if requested.
 */
export class AllDeals extends React.PureComponent {
    static contextType = ApplicationContext
    context!: React.ContextType<typeof ApplicationContext>

    state: { deals: Deal[], isLoading: boolean } = { deals: [], isLoading: true }

    componentDidMount() {
        this.context.dealMan.getDeals().then(deals => {
            this.setState({ deals, isLoading: false })
        })
    }

    render() {
        const ctx = this.context
        const { isLoading, deals } = this.state

        return (
            <div style={ctx.theme.main}>
                <Toolbar />

                <div className='container is-fluid'>
                    <div className='section is-marginless-horizontal'>
                        <div className='columns is-multiline'>
                            {deals.map(deal => {
                                return (
                                    <div key={deal.id} className='column is-3-widescreen is-4 is-12-mobile'>
                                        <DealComponent deal={deal} />
                                    </div>
                                )
                            })}
                        </div>
                        <div className='container'>
                            <button type='button' disabled={isLoading} onClick={async () => {
                                this.setState({ isLoading: true })
                                const y = window.scrollY
                                deals.push(...await ctx.dealMan.getDeals(deals[deals.length - 1].id))
                                this.setState({ deals, isLoading: false })
                                window.scroll(window.scrollX, y)
                            }} className={`button is-primary is-uppercase is-fullwidth ${isLoading ? 'is-loading' : ''}`}>Load More</button>
                        </div>
                    </div>
                </div>
            </div>
        )
    }
}


export const DealComponent = ({ deal }: { deal: Deal }) => {
    const ctx = useContext(ApplicationContext)
    const { addToast } = useToasts()
    const [state, setState] = useState({ loading: false })

    let expiryDate: Moment | undefined = deal.meta && deal.meta.expiryDate ? moment(new Date(deal.meta.expiryDate)) : undefined

    return (
        <div className="card is-unselectable">
            <div className="card-image">
                <figure className="image is-16by9">
                    <img src={deal.cover || require('../logo.svg')} alt={deal.description} />
                </figure>
            </div>
            <div className="card-content has-text-centered">
                {deal.meta ?
                    <div className='title has-text-weight-bold'>
                        {validator.equals(deal.meta!.unit, 'FLAT') ? (
                            `\u20A6 ${deal.value.toLocaleString()}`
                        ) : `${deal.value}%`}
                    </div>
                    : null}
                <p className='title is-5'>{deal.title}</p>
                <p className='subtitle is-6 is-deal-description'>{deal.description}</p>
                {expiryDate ?
                    <span className={`is-positioned-top tag is-medium ${expiryDate.isBefore(Date.now()) ? 'is-danger' : 'is-warning'}`} title={`Deal set to expire on ${expiryDate.toLocaleString()}`}><FaClock />&nbsp; {expiryDate.isBefore(Date.now()) ? 'Expired' : `Until ${expiryDate.calendar()}`}</span>
                    : null}

                <div className="content">
                    <div className='field is-grouped-centered is-grouped'>
                        <div className='control'>
                            <NavLink className='button is-success is-outlined' to={{ state: { deal }, pathname: `/deals/view/${deal.id}` }}>
                                <GiWallet />&nbsp; {ctx.user?.dealExists(deal.id) ? 'View Deal' : 'Get Deal'}
                            </NavLink>
                        </div>

                        {ctx.user?.isVendor && deal.author.id === ctx.user.uid ? (
                            <div className='control'>
                                <button className={`button is-danger ${state.loading ? 'is-loading' : ''}`} onClick={async () => {
                                    if (!window.confirm(`You have tried to delete deal with title "${deal.title}".\r\nDo you want to delete this deal?`)) return

                                    setState({ loading: true })
                                    ctx.dealMan.removeDeal(deal).then(() => addToast("Deal successfully removed!", { appearance: 'success', autoDismiss: true })).catch(e => {
                                        addToast(e.message || "An error occurred!", { appearance: 'error', autoDismiss: true })
                                    }).finally(() => setState({ loading: false }))
                                }}>Delete</button>
                            </div>
                        ) : null}
                    </div>
                </div>

                <div className="media">
                    <div className="media-content">
                        <p className="title is-6"><GiVendingMachine /> {deal.author.CompanyName}</p>
                        <p className="subtitle tags">
                            <span className={`tag ${deal.inStore ? 'is-primary' : 'is-link'}`}>{deal.inStore ? <><FaStore />&nbsp; instore</> : <><FaCloud />&nbsp; online</>}</span>
                        </p>
                    </div>
                </div>
            </div>
        </div >
    )
}