import React, { useState } from 'react';
import { FaThumbsUp } from 'react-icons/fa';
import { BrowserRouter, Route, Switch } from 'react-router-dom';
import { useToasts } from "react-toast-notifications";
import './App.css';
import { Footer } from './components/footer';
import { ApplicationContext, DefaultAppContext } from './lib';
import links from './lib/links';
import { AllDeals, MyDeals } from './pages/deal';
import { ErrorPage } from './pages/error';
import { Home } from './pages/home';
import { Loading } from './pages/loading';
import { LoginUser, LoginVendor } from './pages/login';
import { Logout } from './pages/logout';
import { RegisterUser, RegisterVendor } from './pages/register';
import { DealDetails } from './pages/dealDetails';


const App: React.FC = () => {
  const [ready, setReady] = useState(false)
  const [online, setOnline] = useState(navigator.onLine)

  const { addToast } = useToasts()
  window.onoffline = () => {
    if (!online) return
    setOnline(false)
    addToast("You are offline!", {
      appearance: 'warning',
      autoDismiss: true,
      pauseOnHover: true
    })
  }
  window.ononline = () => {
    if (online) return

    setOnline(true)
    addToast(<p>Welcome back to the Internet &nbsp;<FaThumbsUp /></p>, {
      appearance: 'info',
      autoDismiss: true,
      pauseOnHover: true
    })
  }

  return (
    <ApplicationContext.Provider value={DefaultAppContext}  >
      <ApplicationContext.Consumer>{ctx => {
        ctx.isReady().then(isReady => setReady(isReady))

        return (
          <BrowserRouter basename='/deals'>
            {!ready ? <Loading /> : (
              <>
                <Switch>
                  <Route strict exact path='/deals/view/:id' component={DealDetails} />
                  <Route strict exact path={links.deals} component={AllDeals} />
                  <Route strict exact path={links.myDeals} component={MyDeals} />
                  {/* <Route strict exact path='/deals/:category' component={} /> */}
                  <Route strict exact path="/vendor/register" component={RegisterVendor} />
                  <Route strict exact path="/user/register" component={RegisterUser} />
                  <Route strict exact path="/vendor/login" component={LoginVendor} />
                  <Route strict exact path="/user/login" component={LoginUser} />
                  <Route strict exact path="/:user/logout" component={Logout} />
                  <Route strict exact path="/" component={Home} />
                  <Route component={ErrorPage} />
                </Switch>
                <Footer />
              </>
            )}
          </BrowserRouter>
        )
      }}</ApplicationContext.Consumer>
    </ApplicationContext.Provider>
  );
}

export default App;
