import React from "react";
import { ApplicationContext } from "../lib";

export const Footer = () => (
    <ApplicationContext.Consumer>{ctx => (
        <footer className='footer' style={ctx.theme.footer}>
            <div className="content is-unselectable has-text-centered">
                <p>&copy; {new Date().getFullYear()} {ctx.name} </p>
            </div>
        </footer>
    )}
    </ApplicationContext.Consumer>
)